import Product from "./pages/Product";
import Home from "./pages/Home";
import ProductList from "./pages/ProductList";
import Register from "./pages/Register";
import Login from "./pages/Login";
import Cart from "./pages/Cart";
import './App.css'
import {
  BrowserRouter,
  Routes,
  Route,
} from "react-router-dom";

const App = () => {
  return (
    <BrowserRouter>
      <Routes>
        <Route path="/Ecommerce/" element={<Home />}/>
        <Route path="/Ecommerce/Home" element={<Home />}/>
        <Route path="/Ecommerce/Product" element={<Product />}/>
        <Route path="/Ecommerce/ProductList" element={<ProductList />}/>
        <Route path="/Ecommerce/Shop" element={<ProductList />}/>
        <Route path="/Ecommerce/Register" element={<Register />}/>
        <Route path="/Ecommerce/Login" element={<Login />}/>
        <Route path="/Ecommerce/Cart" element={<Cart />}/>
      </Routes>
    </BrowserRouter>


  )
};

export default App;